@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  min-width: 320px;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40;
}

input {
  border-radius: 0;
}
input::placeholder {
  opacity: 1;
}
input:invalid {
  box-shadow: none;
}

input:matches([type=email], [type=tel], [type=text]) {
  appearance: none;
}

button,
[type=button],
[type=submit] {
  appearance: none;
}

@media (max-width: 767px) {
  body.is-opened {
    overflow-y: hidden;
  }
}

@media (max-width: 767px) {
  body.is-opened::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 43, 102, 0.5);
    width: 11.25%;
    height: 100%;
    z-index: 1;
  }
}

/* Работает в Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #d8d8d9 transparent;
}

/* Работает в Chrome, Edge и Safari */
*::-webkit-scrollbar {
  width: 6px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #d8d8d9;
  border-radius: 10px;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.mobile-only {
  display: none;
}
@media (max-width: 767px) {
  .mobile-only {
    display: block;
  }
}

.container {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 45px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.button {
  display: inline-flex;
  padding: 10px 10px 9px;
  color: #011c40;
  font-size: 14px;
  line-height: 21px;
  font-weight: 600;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  user-select: none;
  justify-content: center;
  text-align: center;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.button:hover, .button:focus {
  opacity: 0.8;
}
.button:active {
  opacity: 0.6;
}

.custom-input {
  position: relative;
  display: flex;
}
.custom-input input {
  width: 100%;
  flex-grow: 1;
  background-color: #f9fbfd;
  border: 2px solid transparent;
  outline: none;
  font-family: inherit;
  color: #011c40;
  padding: 10px 14px 10.5px;
  margin-bottom: 20px;
}
.custom-input input::placeholder {
  color: rgba(1, 28, 64, 0.7);
  font-size: 14px;
  line-height: 21px;
  font-family: inherit;
}
.custom-input input:hover,
.custom-input input:focus {
  background-color: rgba(249, 251, 253, 0.8);
}
.custom-input .input-message {
  font-size: 14px;
  line-height: 16px;
  position: absolute;
  right: 0;
  bottom: 2px;
}
.custom-input .input-message.is-invalid {
  color: #ff0000;
}

.custom-input.is-invalid input {
  border: 2px solid #ff0000;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover {
  border: 2px solid rgba(255, 0, 0, 0.5);
}

.custom-toggle {
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
  display: block;
  margin-bottom: 32px;
  margin-top: 3px;
}
@media (max-width: 767px) {
  .custom-toggle {
    margin-bottom: 27px;
    margin-top: -3px;
  }
}
.custom-toggle label {
  position: relative;
  padding-left: 23px;
  cursor: pointer;
  display: inline-block;
}
@media (max-width: 767px) {
  .custom-toggle label {
    padding-left: 28px;
    width: 80%;
  }
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle input:hover + .custom-toggle__icon, .custom-toggle input:hover + .custom-toggle__icon::before, .custom-toggle input:hover ~ .custom-toggle__label {
  opacity: 0.8;
}
.custom-toggle input:active + .custom-toggle__icon, .custom-toggle input:active + .custom-toggle__icon::before, .custom-toggle input:active ~ .custom-toggle__label {
  opacity: 0.6;
}
.custom-toggle input:focus-visible + .custom-toggle__icon {
  outline: 2px solid #f9fbfd;
}
.custom-toggle span {
  margin-bottom: 0;
  margin-left: 0;
  font-size: 10px;
  line-height: 13px;
}
.custom-toggle .custom-toggle__icon {
  position: absolute;
  top: 50%;
  left: 0;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
  transform: translateY(-50%);
}
@media (max-width: 767px) {
  .custom-toggle .custom-toggle__icon {
    width: 20px;
    height: 20px;
  }
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  content: url("../img/svg/checked.svg");
  position: absolute;
  width: 10px;
  height: 6px;
  left: 3px;
  top: 3px;
}
.custom-toggle .input-message {
  font-size: 14px;
  line-height: 16px;
  position: absolute;
  left: 0;
  bottom: -25px;
}
.custom-toggle .input-message.is-invalid {
  color: #ff0000;
}

.logo {
  display: block;
  line-height: 0;
}
.logo:hover, .logo:focus {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}

.logo__svg {
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .logo__svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__svg {
    width: 42px;
    height: 22px;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: minmax(80px, 130px) minmax(80px, 130px) minmax(80px, 170px) minmax(80px, 130px);
  gap: 15px 34px;
}
@media (max-width: 1023px) {
  .nav__list {
    grid-template-columns: minmax(70px, 113px) minmax(70px, 113px) minmax(70px, 149px) minmax(70px, 113px);
    gap: 15px 24px;
  }
}
@media (max-width: 767px) {
  .nav__list {
    display: flex;
    gap: 0;
    flex-direction: column;
  }
}

.nav__link {
  line-height: 19.5px;
  font-weight: 500;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .nav__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .nav__link {
    font-size: 12px;
    line-height: 14.6px;
  }
}
.nav__link:hover, .nav__link:focus {
  opacity: 0.8;
}
.nav__link:active {
  opacity: 0.6;
}

.social {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
@media (max-width: 767px) {
  .social {
    justify-content: flex-start;
  }
}

.social__item {
  margin-left: 27px;
  margin-top: 9px;
}
@media (max-width: 1023px) {
  .social__item {
    margin-left: 22.4px;
    margin-top: 8px;
  }
}
@media (max-width: 767px) {
  .social__item {
    margin-top: 0;
    margin-left: 0;
    margin-right: 22px;
  }
}

.social__link {
  display: inline-block;
}
.social__link:hover, .social__link:focus {
  opacity: 0.8;
}
.social__link:active {
  opacity: 0.6;
}

.social__svg {
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .social__svg {
    width: 16px;
    height: 16px;
  }
  .social__svg--ok {
    width: 9.6px;
  }
}

.title {
  margin: 0;
  font-size: 28px;
  line-height: 42px;
  font-weight: 600;
  text-align: center;
}
@media (max-width: 1023px) {
  .title {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .title {
    font-size: 24px;
    line-height: 36px;
  }
}

.header {
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.header__nav {
  max-width: 90%;
}
@media (max-width: 767px) {
  .header__nav {
    max-width: 100%;
    display: none;
  }
  .header__nav .nav__item {
    margin-bottom: 26px;
  }
  .header__nav .nav__link {
    color: #011c40;
    padding: 10px 0 8px 10px;
    font-size: 18px;
    line-height: 22px;
    font-weight: 500;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 35px;
  padding-bottom: 35px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding-top: 28px;
    padding-bottom: 28px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    flex-direction: column;
    justify-content: start;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

@media (max-width: 767px) {
  .header__mobile-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}

.header__burger-toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__burger-toggle {
    display: block;
    cursor: pointer;
    position: relative;
    border: none;
    background-color: transparent;
    width: 24px;
    height: 22px;
  }
}

.header__burger-line {
  width: 24px;
  height: 2px;
  background-color: #f9fbfd;
  position: absolute;
  right: 0;
}
.header__burger-line--first {
  top: 4px;
}
.header__burger-line--second {
  top: 10px;
}
.header__burger-line--third {
  bottom: 4px;
}

.header__burger-toggle:focus .header__burger-line,
.header__burger-toggle:hover .header__burger-line {
  opacity: 0.8;
}

.header__burger-toggle:active .header__burger-line {
  opacity: 0.6;
}

@media (max-width: 767px) {
  .is-opened .header .header__wrapper {
    margin-left: 11.25%;
    width: 88.75%;
    background-color: #f9fbfd;
    height: 100vh;
    overflow: scroll;
  }
  .is-opened .header .header__mobile-wrapper {
    margin-bottom: 42px;
  }
  .is-opened .header .header__nav {
    display: block;
    align-self: start;
    margin-bottom: 30px;
  }
  .is-opened .header .logo__svg {
    color: #011c40;
  }
  .is-opened .header .header__burger-line {
    background-color: #011c40;
    width: 18.4px;
  }
  .is-opened .header .header__burger-line--second {
    width: 0;
  }
  .is-opened .header .header__burger-line--first {
    transform: rotate(-45deg) translateY(8px);
  }
  .is-opened .header .header__burger-line--third {
    transform: rotate(45deg) translateY(-8px);
  }
}

.promo {
  background-color: #011c40;
  color: #f9fbfd;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  position: relative;
  padding: 270px 0 278px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-image: url("../img/background/promo-desktop.webp");
}
@media (min-width: 1024px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/background/promo-desktop@2x.webp");
  }
}
@media (max-width: 1023px) {
  .promo {
    padding: 267px 0 275px;
    background-image: url("../img/background/promo-tablet.webp");
    box-shadow: none;
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/background/promo-tablet@2x.webp");
  }
}
@media (max-width: 767px) {
  .promo {
    padding: 175px 0 179px;
    background-image: url("../img/background/promo-mobile.webp");
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/background/promo-mobile@2x.webp");
  }
}

.promo h1 {
  margin: 0;
  margin-left: 95px;
  margin-bottom: 12px;
  font-size: 54px;
  line-height: 65.8px;
  font-weight: 700;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .promo h1 {
    margin-left: 0;
    margin-bottom: 13px;
    font-size: 50px;
    line-height: 61px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .promo h1 {
    margin-bottom: 12px;
    font-size: 36px;
    line-height: 45px;
  }
}

.promo p {
  margin: 0;
  margin-left: 95px;
  font-size: 20px;
  line-height: 24.4px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .promo p {
    margin-left: 0;
    font-size: 18px;
    line-height: 22px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .promo p {
    font-size: 16px;
    max-width: 80%;
  }
}

.about {
  display: flex;
  justify-content: space-between;
  padding-top: 117px;
  padding-bottom: 145px;
}
@media (max-width: 1023px) {
  .about {
    padding-top: 91px;
    padding-bottom: 94px;
    align-items: start;
  }
}
@media (max-width: 767px) {
